.App {
  margin-top: 50px;
}
.centered
{
  margin: 0 auto;
  text-align: center;
  background-color: rgb(151, 101, 197);
  width: max-content; 
  padding: 30px;
  border-radius: 10px;
  box-shadow: 1px 1px 10px ;
}

input
{
  background-color: beige;
  padding: 10px;
  border: 1px solid purple;
  border-radius: 5px;
  margin: 5px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
}

li
{
  list-style-type: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 17px;
  color: rgb(22, 21, 21);
  margin: 10px;
}
.pinky
{
  background-color: pink;
  border-radius: 50%;
  padding: 10px;
}
.kontak
{
  background-color: rgba(118, 8, 221, 0.658);
  padding: 5px;
}
.butonKeeper
{
  display: flex;
  justify-content:center;
}
.btn
{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
  border: 2px solid pink;
  background-color: rgb(32, 12, 207);
  color: white;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;

}